@import url('./reset.css');
@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');

:root {
  --font1: 24px;
  --font2: 18px;
  --font3: 70px;
}

body {
  background-color: #1e1f26;
  font-family: 'Roboto', sans-serif;
  font-size: 24px;
}

.main {
  height: 100vh;
  display: flex;
}

/* =========================== LOADER ============================= */

#loader {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #1e1f26b4;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 9999;
}

#loader .bar {
  position: relative;

  width: 50px;
  height: 50px;
  border-radius: 100%;
  border: 5px solid rgba(255, 255, 255, 0.253);
  border-top-color: #ffffff;
  animation: spin 0.8s infinite linear;
  top: 0.5%;
}

@keyframes loadingBar {
  0% {
    transform: translateX(0);
  }
  50% {
    transform: translateX(50px);
  }
  100% {
    transform: translateX(0);
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes spin {
  100% {
    transform: rotate(360deg);
  }
}


/* ======================== LOADER-DISCONECTED ======================= */

#loader-disconected {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #1e1f26b7;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 9999;
}

#loader-disconected .bar-dis {
